.about__description {
  color: #041827;
}
.about__description h3 {
  margin: 0;
  margin-bottom: 12px;
  font-family: "Roboto-condensed", "Arial", sans-serif;
  font-size: 36px;
  line-height: 51px;
}
.about__description p {
  line-height: 22px;
}

.about__text {
  margin: 0;
  margin-bottom: 20px;
}

.about__text-more {
  margin: 0;
  display: none;
}

.about__text-more--opened {
  display: block;
}

.about__button {
  display: flex;
  justify-content: center;
  min-width: 180px;
  margin-top: 40px;
  padding: 13px 25px;
  background-color: transparent;
  border: 1px solid #00b2ff;
  color: #00b2ff;
}

.main-footer {
  background-color: #041827;
}

.main-footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 50px;
}

.main-footer__socials {
  margin-top: 37px;
  align-self: center;
}

.main-footer__socials-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  max-width: 150px;
  margin: 0;
  padding: 0;
  margin-left: 15px;
  list-style: none;
}
@media (hover: hover) {
  .main-footer__socials-list a:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-footer__socials-list a.focus-visible:focus {
  opacity: 0.8;
}

.main-footer__header h2 {
  font-family: "Roboto-condensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
}

.main-footer__nav-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 48px;
}

.main-footer__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-footer__nav-list a {
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
}

.main-footer__contacts-adress p {
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
}

.main-footer__contacts-phone {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-footer__contacts-phone a {
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
}

