.intro {
  min-height: 784px;
  margin-top: 95px;
  background-image: url("../img/intro.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: $color-black-pearl;

  @include vp-1023 {
    min-height: 540px;
    margin-top: -15px;
    background-position: -135px -101px;
    background-size: 1462px auto;
}

  @include vp-767 {
    min-height: 490px;
    margin-top: -10px;
    background-position: -195px -30px;
    background-size: 1157px auto;
  }

  & h1 {
    margin: 0;
    font-family: $ff-roboto-condensed;
    font-weight: 700;
    font-size: 80px;
    line-height: 94px;
    letter-spacing: 0.07em;
    text-transform: uppercase;

    color: $color-alice-blue;

    @include vp-767 {
      font-size: 36px;
      line-height: 42px;
      margin-bottom: 8px;
    }

    @include vp-1023 {
      font-size: 64px;
      line-height: 75px;
      margin-bottom: 0;
    }
  }

  & p {
    margin: 0;
    margin-top: 30px;
    margin-bottom: 50px;
    max-width: 685px;
    font-size: 22px;
    line-height: 155%;

    color: $color-alice-blue;

    @include vp-1023 {
      margin-bottom: 57px;
      margin-top: 0;
    }

    @include vp-767 {
      font-size: 14px;
      margin-bottom: 45px;
      margin-top: 0;
    }
  }

  & a {
    display: block;
    max-width: 410px;
    padding: 15px 25px;
    font-size: 18px;
    line-height: 21px;
    text-transform: uppercase;
    letter-spacing: 0.05em;

    background-color: $color-deep-sky-blue;
    color: $color-alice-blue;

    @include vp-1023 {
      margin-left: 45px;
    }

    @include vp-767 {
      width: 100%;
      margin-left: 0;
      padding: 12px;
      font-size: 14px;
    line-height: 16px;
    }
  }
}

.intro__text {
  font-size: 18px;
  line-height: 21px;

  &--mobile {
    display: none;
  }

  @include vp-767 {
    display: none;

    &--mobile {
      display: block;
    }
  }
}

.intro__wrapper {
  padding-top: 210px;
  padding-left: 178px;

  @include vp-767 {
    padding-top: 90px;
    padding-bottom: 190px;
  }

  @include vp-1023 {
    padding-top: 95px;
    padding-bottom: 205px;
  }
}
